// 見出し
//
// ページ内に使われている見出しについて。<br>
// ページ見出し、セクション毎の見出し、セクション内のさらに小見出しの3つに大きく分かれる。<br>
// メーカー検索ページではメーカー説明用の見出しを使っている。<br>
// コラムおよび初心者ガイドではアイキャッチ画像用の見出しを使っている。
//
// Markup:
// <h1 class="ttl-page">ページ見出し.ttl-page</h1>
// <h2 class="ttl-section">セクション毎の見出し.ttl-section</h2>
// <h3 class="ttl-block">セクション内の小見出し.ttl-block</h3>
//
// Styleguide 1.0.0

//------------------------------------------------------------
// title
//------------------------------------------------------------

.ttl {
	&-page {
		position: relative;
		width: $pcContentsWidth;
		margin: 0 auto;
		padding: 30px 0;
		color: colors(ttl);
		@include fz(40);
		font-weight: 700;
		line-height: 1.25;
		text-align: left;
		text-shadow: 2px 2px 0 colors(col-white);
		box-sizing: border-box;
		z-index: 20;
		@include break-point(tblandscape) {
			width: $tbContentsWidth;
			@include padding(null 10px null 10px);
		}
	}
	&-section {
		position: relative;
		top: -6px;
		margin-bottom: 5px;
		padding-bottom: 3px;
		border-bottom: 3px solid colors(col-xlightgray);
		color: colors(ttl);
		@include fz(20);
		font-weight: 700;
		font-feature-settings : "pkna";
		line-height: 1.25;
		& .section-word {
			@include inline-block;
			position: relative;
			top: 6px;
			padding-bottom: 3px;
			border-bottom: 3px solid colors(ttl);
			font-weight: 700;
		}
		& .updated-date {
			display: inline-block;
			position: absolute;
			bottom: 8px;
			right: 0;
			color: colors(basetxt);
			@include fz(12);
			font-weight: 400;
			line-height: 1;
			text-align: right;
		}
		.footer-area & {
			margin-bottom: 5px;
			color: colors(col-gray);
			@include fz(16);
			line-height: 1.25;
			& .section-word {
				border-bottom: 3px solid colors(col-gray);
			}
		}
	}
	&-block {
		margin-bottom: 5px;
		@include fz(18);
		font-weight: 700;
		.footer-area & {
			color: colors(col-gray);
			@include fz(16);
		}
	}
	&-make {
		margin-bottom: 10px;
		@include fz(20);
		font-weight: 700;
		line-height: 1.25;
		.make-img {
			@include inline-block;
			width: 36px;
			height: 28px;
			margin-right: 5px;
			img {
				width: auto;
				max-width: 100%;
				height: auto;
				vertical-align: text-top;
			}
		}
	}
	&-column-img {
		position: relative;
		@include margin(null null 20px null);
		text-align: center;
		@include break-point(tbportrait) {
			@include inline-block;
			left: 50%;
			@include translate(-50%, 0);
		}
		.news-page & {
			border: 1px solid colors(col-lightgray);
		}
		.column-img-txt {
			display: block;
			position: absolute;
			top: 50%;
			left: 20px;
			@include fz(24);
			line-height: 2;
			text-align: left;
			text-shadow: 2px 2px 0 colors(col-white), 2px 2px 0 colors(col-white), 2px 2px 0 colors(col-white), 2px 2px 0 colors(col-white), 2px 2px 0 colors(col-white);
			@include translate(0, -50%);
			z-index: 2;
		}
		img {
			position: relative;
			width: auto;
			max-width: 100%;
			height: auto;
			z-index: 1;
			.news-page & {
				width: 100%;
			}
		}
	}
}
