// ブロック単位で各ページで共通で使うものをここで記載する

//------------------------------------------------------------
// block
//------------------------------------------------------------

//=========================
// メインビジュアル
//=========================

.mv-area {
	margin: 0 auto;
	border-bottom: 3px solid colors(disable);
	background: rgb(255,255,255);
	background: -moz-linear-gradient(top,  rgba(255,255,255,1) 20%, rgba(192,234,255,1) 100%);
	background: -webkit-linear-gradient(top,  rgba(255,255,255,1) 20%,rgba(192,234,255,1) 100%);
	background: linear-gradient(to bottom,  rgba(255,255,255,1) 20%,rgba(192,234,255,1) 100%);
	filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#c0eaff',GradientType=0 );
	text-align: center;
	@include break-point(mobile) {
		width: $pcContentsWidth;
	};
	&.no-mv-assess {
		background: -moz-linear-gradient(top,  rgba(255,255,255,1) 50%, rgba(192,234,255,1) 100%);
		background: -webkit-linear-gradient(top,  rgba(255,255,255,1) 50%,rgba(192,234,255,1) 100%);
		background: linear-gradient(to bottom,  rgba(255,255,255,1) 50%,rgba(192,234,255,1) 100%);
		filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#c0eaff',GradientType=0 );
	}
	&.no-mv-cc {
		padding-bottom: 0.5em;
		background: -moz-linear-gradient(top,  rgba(255,255,255,1) 75%, rgba(192,234,255,1) 100%);
		background: -webkit-linear-gradient(top,  rgba(255,255,255,1) 75%,rgba(192,234,255,1) 100%);
		background: linear-gradient(to bottom,  rgba(255,255,255,1) 75%,rgba(192,234,255,1) 100%);
		filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#c0eaff',GradientType=0 );
	}
	&-inner {
		display: table;
		position: relative;
		width: $pcContentsWidth;
		margin: 0 auto 30px;
		text-align: left;
		table-layout: fixed;
		z-index: 5;
		@include break-point(tblandscape) {
			width: $tbContentsWidth;
			@include padding(null 10px null 10px);
			box-sizing: border-box;
		};
		&.one-column {
			display: block;
		}
	}
}

.mv-left-block {
	@include table-cell;
	position: relative;
	width: 540px;
	vertical-align: top;
	z-index: 2;
	@include break-point(tbportrait) {
		display: block;
		width: 100%;
	};
	@include break-point(mobile) {
		@include table-cell;
		width: 540px;
	};
}

.mv-right-block {
	@include table-cell;
	position: relative;
	padding-left: 20px;
	vertical-align: top;
	@include break-point(tbportrait) {
		display: block;
		width: 100%;
		padding-left: 0;
	};
	@include break-point(mobile) {
		@include table-cell;
		width: auto;
		padding-left: 20px;
	};
}

// キャッチコピー
//------------------------------------------------------------

.mv-cc-txt {
	position: relative;
	@include fz(22);
	line-height: 1.5;
	font-weight: 700;
	text-shadow: 2px 2px 0 colors(col-white);
	z-index: 10;
}

// 査定モジュール
//------------------------------------------------------------

.assessment-module {
	&-block {
		position: relative;
		margin-top: 26px;
		border: 4px solid colors(col-orange);
		background-color: colors(col-white);
		font-feature-settings : "palt";
		// サブカラムの場合
		.sub-content & {
			margin-top: 0;
		}
	}
	&-ttl {
		@include padding(5px null 10px 60px);
		background: colors(col-orange);
		color: colors(col-white);
		@include fz(26);
		font-weight: 700;
		line-height: 1;
		text-align: left;
		box-sizing: border-box;
		@include break-point(tblandscape) {
			padding-left: 100px;
			@include fz(22);
		};
		@include break-point(tbportrait) {
			padding-left: 110px;
			@include fz(24);
		};
		@include break-point(mobile) {
			@include fz(22);
		};
		// メインカラム内の場合
		.in-main & {
			padding-left: 114px;
			@include fz(28);
		}
		// 1カラムレイアウトの場合
		.one-column & {
			padding-left: 64px;
			@include fz(34);
			@include break-point(tblandscape) {
				padding-left: 100px;
				@include fz(30);
			};
			@include break-point(tbportrait) {
				@include padding(10px null 15px 110px);
				@include fz(22);
				.fsize-48 {
					@include fz(32);
				}
			};
		}
		// サブカラムの場合
		.sub-content & {
			padding: 0 0 10px;
			@include fz(16);
			line-height: 1.5;
			text-align: center;
			@include break-point(tblandscape) {
				@include fz(14);
				.fsize-18 {
					@include fz(16);
				}
			};
			@include break-point(tbportrait) {
				@include fz(20);
				.fsize-18 {
					@include fz(20);
				}
				br {
					display: none;
				}
			};
		}
	}
	&-body {
		padding: 10px 20px 20px 20px;
		background: colors(col-white);
		// メインカラム内の場合
		.in-main & {
			padding: 10px 100px;
		}
		// サブカラムの場合
		.sub-content & {
			padding: 10px;
		}
		// 1カラムレイアウトの場合
		.one-column & {
			display: table;
			width: 100%;
			padding-bottom: 10px;
			box-sizing: border-box;
			@include break-point(tbportrait) {
				display: block;
			};
			&-left {
				@include table-cell;
				width: 472px;
				vertical-align: top;
				@include break-point(tbportrait) {
					display: block;
					width: auto;
				};
			}
			&-right {
				@include table-cell;
				@include padding(10px null null 30px);
				vertical-align: top;
				@include break-point(tbportrait) {
					display: block;
					padding: 0;
				};
			}
		}
	}
	&-form {
		&-box {
			margin-bottom: 20px;
			// サブカラムの場合
			.sub-content & {
				margin-bottom: 15px;
			}
		}
		&-row {
			display: table;
			width: 100%;
			margin: 10px 0;
			table-layout: fixed;
			// サブカラムの場合
			.sub-content & {
				margin: 0 0 10px;
			}
			&-ttl {
				@include table-cell;
				width: 110px;
				padding: 0 10px 0 20px;
				color: colors(col-blue);
				font-weight: 700;
				// サブカラムの場合
				.sub-content & {
					width: 90px;
					padding: 0;
				}
			}
			&-body {
				@include table-cell;
				// サブカラムの場合
				.sub-content & {
					padding: 0;
				}
				a, label {
					margin-right: 8px;
					&:last-of-type {
						margin-right: 0;
					}
					// サブカラムの場合
					.sub-content & {
						@include inline-block;
					}
				}
				.input-zip {
					width: 7rem;
				}
				select {
					width: 100%;
				}
				// サブカラムの場合(タブレット対応)
				.sub-content & {
					@include break-point(tbportrait) {
						a, label {
							margin-right: 8px;
							&:last-of-type {
								margin-right: 0;
							}
						}
						br {
							display: none;
						}
					};
				}
			}
		}
	}
}

.assesment-btn-box {
	margin: 20px 0;
	line-height: 1;
	// 1カラムレイアウトの場合
	.one-column & {
		margin-bottom: 5px;
	}
	// サブカラムの場合
	.sub-content & {
		margin: 0;
	}
}

.assesment-other-service-box {
	margin: 20px 0;
	@include fz(14);
	text-align: center;
	// 1カラムレイアウトの場合
	.one-column & {
		margin-bottom: 5px;
	}
	// サブカラムの場合
	.sub-content & {
		margin: 0;
		text-align: left;
	}
	a {
		display: inline-block;
		margin: 0 10px;
		// サブカラムの場合
		.sub-content & {
			display: block;
			margin: 10px 0;
		}
	}
}

.assesment-tel {
	&-box {
		display: table;
		width: calc(100% - 2px);
		margin: 10px 0;
		border: 1px solid colors(ttl);
		line-height: 1;
		table-layout: fixed;
		// 1カラムレイアウトの場合
		.one-column & {
			display: block;
			margin: 0;
		}
	}
	&-ttl {
		@include table-cell;
		width: 144px;
		padding: 0 5px;
		background: colors(ttl);
		color: colors(col-white);
		@include fz(16);
		font-weight: 700;
		vertical-align: middle;
		white-space: nowrap;
		// 1カラムレイアウトの場合
		.one-column & {
			display: block;
			width: auto;
			padding: 10px 5px;
			@include fz(26);
			text-align: center;
			@include break-point(tblandscape) {
				@include fz(22);
			};
			@include break-point(tbportrait) {
				@include table-cell;
				width: 144px;
				padding: 0 5px;
				@include fz(16);
				text-align: left;
				white-space: normal;
			};
		}
	}
	&-body {
		@include table-cell;
		position: relative;
		padding: 5px 0 5px 20px;
		vertical-align: middle;
		overflow: hidden;
		// 1カラムレイアウトの場合
		.one-column & {
			display: block;
			padding: 15px 10px;
			@include break-point(tbportrait) {
				@include table-cell;
				position: relative;
				padding: 5px 0 5px 20px;
				vertical-align: middle;
				overflow: hidden;
				&:after {
					position: absolute;
					@include arrow(right, 94, colors(ttl));
					border-left-width: 16px;
					border-right-width: 16px;
				}
			};
			&:after {
				position: static;
				border: none;
			}
		}
		&:after {
			position: absolute;
			top: 50%;
			left: 0px;
			@include arrow(right, 94, colors(ttl));
			border-left-width: 16px;
			border-right-width: 16px;
			@include translate(0, -50%);
			z-index: 2;
			content: "";
		}
	}
	&-num {
		@include inline-block;
		clear: both;
		margin-bottom: 5px;
		color: colors(ttl);
		@include fz(42);
		font-weight: 900;
		@include break-point(tbportrait) {
			display: block;
		}
		// 1カラムレイアウトの場合
		.one-column & {
			margin-left: 45px;
			@include fz(52);
			@include break-point(tblandscape) {
				margin-left: 0;
			};
			@include break-point(tbportrait) {
				@include fz(42);
			};
			img {
				@include inline-block;
				margin-right: 10px;
			}
		}
	}
	&-free {
		@include inline-block;
		clear: both;
		margin-bottom: 5px;
		font-weight: 700;
		@include break-point(tbportrait) {
			display: block;
		}
		// 1カラムレイアウトの場合
		.one-column & {
			margin-left: 110px;
			@include break-point(tblandscape) {
				margin-left: 63px;
			};
		}
	}
	&-time {
		@include inline-block;
		clear: both;
		@include fz(12);
		@include break-point(tbportrait) {
			display: block;
		}
		// 1カラムレイアウトの場合
		.one-column & {
			margin-left: 110px;
			@include break-point(tblandscape) {
				margin-left: 63px;
			};
		}
	}
}

.assessment-jpuc {
	&-box {
		display: table;
		table-layout: fixed;
	}
	&-ttl {
		@include table-cell;
		width: 60px;
		height: 50px;
		vertical-align: middle;
	}
	&-body {
		@include table-cell;
		padding-left: 5px;
		@include fz(12);
		vertical-align: middle;
	}
}

.ico-use-record {
	position: absolute;
	left: -55px;
	top: -30px;
	width: 114px;
	height: 113px;
	@include rotate(-15);
	@include break-point(tblandscape) {
		left: -14px;
	};
	@include break-point(tbportrait) {
		left: -4px;
	};
	// メインカラム内の場合
	.in-main & {
		left: -4px;
	}
}

// メインビジュアル右側
//------------------------------------------------------------

// 査定事例のブロック

.assessment-example {
	&-box {
		position: relative;
		max-height: 600px; // 明示的にmax-heightを決めないと、bxsliderのコンテンツが邪魔してサブカラムの追従査定モジュールが誤動作起こすので、指定せざるを得なかった
		margin-top: 20px;
		overflow: hidden;
		z-index: 3;
		&:after {
			position: absolute;
			top: 50%;
			left: 50%;
			width: 300px;
			height: 230px;
			@include border-radius(50%,50%,50%,50%);
			background: colors(col-white);
			@include box-shadow(0,0,50px,60px,rgba(colors(col-white),1));
			@include translate(-50%, -50%);
			z-index: -1;
			content: "";
		}
		@include break-point(tbportrait) {
			width: 510px;
			left: 50%;
			@include translate(-50%, 0%);
		};
		// ふきだしがないタイプの査定事例
		&.no-balloon {
			height: auto;
		}
	}
	&-group {
		position: relative;
		z-index: 4;
	}
	&-list {
		height: 500px;
		text-align: center;
		.no-balloon & {
			height: auto;
			min-height: 450px;
		}
	}
	&-car {
		&-ttl {
			display: block;
			position: relative;
			left: 50%;
			width: 360px;
			margin-bottom: 10px;
			@include fz(20);
			font-weight: 700;
			text-align: left;
			@include translate(-50%,0%);
			z-index: 5;
		}
		&-cctxt {
			@include inline-block;
			position: relative;
			left: 0;
			min-width: 360px;
			margin-bottom: 20px;
			@include fz(18);
			font-weight: 700;
			text-align: left;
			z-index: 5;
		}
		&-photo {
			display: block;
			position: relative;
			top: 170px;
			left: 50%;
			width: 360px;
			height: 180px;
			transform: translateX(-50%);
			.no-balloon & {
				@include inline-block;
				top: auto;
				left: auto;
				margin-bottom: 10px;
				transform: translateX(0);
			}
			img:not(.ico-timing-to-sell) {
				width: auto;
				max-width: 100%;
				height: auto;
				max-height: 100%;
				@include box-shadow(0,0,10px,10px,rgba(colors(col-white),1));
			}
			.ico-timing-to-sell {
				position: absolute;
				top: -15px;
				left: -50px;
				z-index: 8;
			}
		}
		&-info {
			position: absolute;
			left: 50%;
			bottom: 30px;
			width: 360px;
			z-index: 5;
			@include translate(-50%,0%);
			text-shadow: 0px 0px 3px colors(col-white), 0px 0px 3px colors(col-white), 0px 0px 3px colors(col-white), 0px 0px 3px colors(col-white);
			.no-balloon & {
				position: relative;
				bottom: auto;
			}
		}
		&-price {
			@include fz(20);
			font-weight: 700;
			text-shadow: 0px 2px 0px rgba(colors(col-black), 0.2);
		}
	}
}

.price-balloon {
	position: absolute;
	width: 120px;
	height: 136px;
	font-weight: 700;
	@include opacity(0);
	z-index: 4;
	&.balloon-a {
		top: 40px;
		left: 40px;
	}
	&.balloon-b {
		top: 40px;
		left: 170px;
	}
	&.balloon-c {
		top: 20px;
		left: 300px;
		width: 140px;
		height: 159px;
	}
	& .price-balloon-txt {
		position: relative;
		top: 24px;
		padding: 0 5px 0 0;
		line-height: 1;
		z-index: 6;
		& .great-txt {
			color: colors(col-yellow);
		}
		& .flash01 {
			position: absolute;
			top: 14px;
			left: 1px;
			z-index: 8;
		}
		& .flash02 {
			position: absolute;
			top: 46px;
			left: 50px;
			z-index: 8;
		}
		& .flash03 {
			position: absolute;
			top: 30px;
			left: 88px;
			z-index: 8;
		}
	}
	&.balloon-c .price-balloon-txt {
		top: 26px;
		color: colors(col-white);
		text-shadow: 0 0 3px rgba(colors(col-black),0.5);
	}
	&-group {
		position: relative;
	}
	&-base {
		position: absolute;
		left: 0;
		top: 0;
		z-index: 5
	}
}

.price-difference-txt {
	position: absolute;
	top: 50%;
	left: 50%;
	width: 100%;
	color: colors(col-red);
	@include fz(36);
	font-weight: 700;
	line-height: 1;
	text-shadow: 0 0 8px colors(col-white),0 0 8px colors(col-white),0 0 8px colors(col-white),0 0 8px colors(col-white),0 0 8px colors(col-white),0 0 8px colors(col-white);
	@include opacity(0);
	@include translate(-50%,-50%);
	z-index: 12;
}

.example-car-info-group {
	display: table;
	width: 100%;
	@include fz(12);
	table-layout: fixed;
	& .example-car-info-ttl {
		@include table-cell;
		width: 100px;
		font-weight: 700;
		text-align: right;
		vertical-align: top;
		white-space: nowrap;
		.pickup-review-area & {
			width: 60px;
			font-weight: normal;
		}
	}
	& .example-car-info-body {
		@include table-cell;
		font-weight: 700;
		text-align: left;
		vertical-align: top;
		word-wrap: break-word;
		overflow-wrap : break-word;
		.pickup-review-area & {
			font-weight: normal;
		}
	}
}

.prev-arrow,.next-arrow,
.prev-arrow-pickup,.next-arrow-pickup {
	@include inline-block;
	position: absolute;
	top: 50%;
	line-height: 1;
	cursor: pointer;
	@include translate(0,-50%);
	z-index: 10;
	a {
		@include inline-block;
		color: colors(link);
		@include fz(72);
		font-weight: 700;
		text-decoration: none;
		@include scale(0.5,1)
			}
}
.prev-arrow {
	left: -10px;
}
.next-arrow {
	right: -10px;
}
.prev-arrow-pickup {
	left: -60px;
}
.next-arrow-pickup {
	right: -60px;
}

// 査定会社のブロック

.merchant {
	&-box {
		position: absolute;
		bottom: 0;
		padding: 5px 8px;
		border: 1px solid colors(ttl);
		background-color: colors(col-white);
		text-align: center;
		z-index: 7;
		@include break-point(tbportrait) {
			position: static;
		};
		.one-column & {
			position: static;
		}
		&.in-main {
			position: static;
		}
	}
	&-ttl {
		color: colors(ttl);
		@include fz(20);
		font-weight: 700;
		@include break-point(tblandscape) {
			@include fz(18);
		};
		@include break-point(tbportrait) {
			@include fz(20);
		};
	}
	&-logo {
		&-group {
			@include fz(0);
		}
		&-list {
			@include inline-block;
			margin: 3px;
			.in-main & {
				@include margin(6px 9px 6px 9px);
			}
		}
	}
}

//=========================
// コンテンツ領域
//=========================

.content {
	display: table;
	width: $pcContentsWidth;
	margin: 30px auto;
	@include break-point(tblandscape) {
		@include flex-block;
		width: $tbContentsWidth;
		-webkit-align-items: stretch;
		align-items: stretch;
	};
	@include break-point(tbportrait) {
		display: block;
	};
	@include break-point(mobile) {
		display: table;
		width: $pcContentsWidth;
	};
}

// メインカラム
//------------------------------------------------------------

.main-content {
	@include table-cell;
	position: relative;
	width: $pcMainWidth;
	@include padding(30px 30px 45px 30px);
	border: 1px solid colors(disable);
	@include border-radius(3px,3px,3px,3px);
	@include fz(16);
	@include box-shadow(0,0,5px,0,rgba(0,0,0,0.2));
	vertical-align: top;
	box-sizing: border-box;
	@include break-point(tblandscape) {
		display: block;
		width: $tbMainWidth;
		margin-left: 10px;
		@include padding(20px 20px 45px 20px);
	};
	@include break-point(tbportrait) {
		display: block;
		width: calc(100% - 20px);
