//------------------------------------------------------------
// Nav
//------------------------------------------------------------

//=========================
// グローバルナビ/補足ナビ
//=========================

.nav-block {
	@include table-cell;
	vertical-align: bottom;
}

// グローバルナビ
//------------------------------------------------------------

.global-nav {
	float: right;
	clear: both;
	@include fz(0);
	&-list {
		@include inline-block;
		margin-right: 15px;
		@include fz(18);
		vertical-align: bottom;
		@include break-point(tbportrait) {
			margin-right: 10px;
			@include fz(16);
		};
		&:last-child {
			margin-right: 0;
		}
		& a {
			display: block;
			padding: 1rem 1.5rem;
			@include border-radius(3px,3px,0,0);
			background: colors(link);
			color: colors(col-white);
			font-weight: 700;
			text-decoration: none;
			@include opacity(1);
			@include box-shadow(3px,0,0,0,rgba(colors(col-black),0.2));
			box-sizing: border-box;
			cursor: default;
		}
		& .ico-arrow {
			color: colors(col-white);
			@include transition(0.3s);
			@include rotate(0);
			@include break-point(mobile) {
				display: none;
			};
		}
		&:hover .ico-arrow {
			@include rotate(90);
		}
		// グローバルナビ展開
		& a + .extend-global-nav {
			position: absolute;
			border: 1px solid colors(col-white);
			visibility: hidden;
			box-sizing: border-box;
			@include box-shadow(0,1px,3px,0,rgba(colors(col-black),0.7));
			@include opacity(0);
			@include transition(0.5s);
			z-index: 200;
			&:before {
				position: absolute;
				top: -14px;
				left: 20%;
				margin-left: -13px;
				@include arrow(top,14,colors(col-white));
				content: "";
			}
			&:after {
				position: absolute;
				top: -12px;
				left: 20%;
				margin-left: -12px;
				@include arrow(top,12,colors(col-blue));
				content: "";
			}
			& .extend-global-nav-list {
				display: block;
				@include fz(14);
				& a {
					padding: 1rem;
					border-top: none;
					border-top: 1px solid colors(col-white);
					@include border-radius(0,0,0,0);
					background: colors(col-blue);
					font-weight: normal;
					@include box-shadow(0,0,0,0);
					cursor: pointer;
					&:hover {
						background: lighten(colors(col-blue), 10%);
					}
				}
				&:first-child a {
					border-top: none;
				}
				& .ico-arrow {
					margin-bottom: 0.2rem;
					@include rotate(0);
				}
				&:hover .ico-arrow {
					@include rotate(0);
					@include translate(2px,0);
				}
			}
		}
		&:hover a + .extend-global-nav {
			visibility: visible;
			@include opacity(1);
		}
	}
}

// 補足ナビ
//------------------------------------------------------------

.supplement-nav {
	float: right;
	clear: both;
	margin-bottom: 20px;
	@include fz(0);
	&-list {
		@include inline-block;
		margin-right: 15px;
		@include fz(12);
		vertical-align: top;
		& a {
			&:link {
				color: colors(basetxt);
				vertical-align: bottom;
			}
			&:hover {
				text-decoration: none;
			}
		}
		&:last-child {
			margin-right: 0;
		}
	}
}

//=========================
// ページャ
//=========================

.pager-nav {
	display: block;
	margin: 20px 0;
	@include fz(0);
	text-align: center;
	&-list {
		@include inline-block;
		@include fz(14);
		margin: 0 5px;
		&-btn {
			@include btn;
			position: static;
			@include table-cell;
			width: 44px;
			height: 44px;
			padding: 0;
			border: 1px solid colors(col-lightgray);
			background-color: colors(col-white);
			color: colors(basetxt);
			vertical-align: middle;
			@include box-shadow(0,0,0,0,rgba(colors(col-black),0.0));
			cursor: default;
			&:hover {
				@include box-shadow(0,0,0,0,rgba(colors(col-black),0.0));
			}
		}
		&-btn[href] {
			position: relative;
			border: 1px solid colors(link);
			background-color: colors(link);
			color: colors(col-white);
			font-weight: normal;
			vertical-align: middle;
			@include box-shadow(0,5px,0,0,rgba(colors(col-black),0.2));
			cursor: pointer;
			&:hover {
				@include box-shadow(0,4px,0,0,rgba(colors(col-black),0.2));
			}
			&:active {
				@include box-shadow(0,0,0,0,rgba(colors(col-black),0.0));
			}
		}
	}
}