  vertical-align: baseline;
}

input[type="radio"], input[type="checkbox"] {
  display: none;
}

input[type="radio"] + label, input[type="checkbox"] + label {
  display: inline-block;
  position: relative;
  padding: 3px 0 3px 26px;
  cursor: pointer;
}

*:first-child + html input[type="radio"] + label, *:first-child + html input[type="checkbox"] + label {
  display: inline;
  zoom: 1;
}

input[type="radio"] + label:before, input[type="checkbox"] + label:before {
  position: absolute;
  top: 4px;
  left: 0;
  width: 20px;
  height: 20px;
  border: 2px solid #e6e6e6;
  background-color: white;
  content: "";
  -webkit-transition: 0.3s;
  -moz-transition: 0.3s;
  -ms-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}

input[type="radio"] + label:after, input[type="checkbox"] + label:after {
  position: absolute;
  content: "";
  -webkit-transition: 0.3s;
  -moz-transition: 0.3s;
  -ms-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}

input[type="radio"]:checked + label:before, input[type="checkbox"]:checked + label:before {
  border: 2px solid #cccccc;
}

input[type='radio'] + label:before {
  -webkit-border-radius: 50% 50% 50% 50%;
  -moz-border-radius: 50% 50% 50% 50%;
  -ms-border-radius: 50% 50% 50% 50%;
  -o-border-radius: 50% 50% 50% 50%;
  border-radius: 50% 50% 50% 50%;
}

input[type='radio'] + label:after {
  top: 10px;
  left: 6px;
  width: 12px;
  height: 12px;
  -webkit-border-radius: 50% 50% 50% 50%;
  -moz-border-radius: 50% 50% 50% 50%;
  -ms-border-radius: 50% 50% 50% 50%;
  -o-border-radius: 50% 50% 50% 50%;
  border-radius: 50% 50% 50% 50%;
  background-color: rgba(230, 230, 230, 0.5);
}

input[type='radio']:checked + label:after {
  background-color: #6b96fa;
  -webkit-box-shadow: 1px 1px 2px 0 rgba(0, 0, 0, 0.45) inset;
  -moz-box-shadow: 1px 1px 2px 0 rgba(0, 0, 0, 0.45) inset;
  -ms-box-shadow: 1px 1px 2px 0 rgba(0, 0, 0, 0.45) inset;
  -o-box-shadow: 1px 1px 2px 0 rgba(0, 0, 0, 0.45) inset;
  box-shadow: 1px 1px 2px 0 rgba(0, 0, 0, 0.45) inset;
}

input[type='checkbox'] + label:after {
  top: 8px;
  left: 3px;
  width: 14px;
  height: 6px;
  border-left: 4px solid rgba(230, 230, 230, 0.5);
  border-bottom: 4px solid rgba(230, 230, 230, 0.5);
  -webkit-transform: rotate(-45deg);
  -moz-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  -o-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

input[type='checkbox']:checked + label:after {
  border-left: 4px solid #6b96fa;
  border-bottom: 4px solid #6b96fa;
  -webkit-box-shadow: -1px 1px 1px 0 rgba(0, 0, 0, 0.45) ;
  -moz-box-shadow: -1px 1px 1px 0 rgba(0, 0, 0, 0.45) ;
  -ms-box-shadow: -1px 1px 1px 0 rgba(0, 0, 0, 0.45) ;
  -o-box-shadow: -1px 1px 1px 0 rgba(0, 0, 0, 0.45) ;
  box-shadow: -1px 1px 1px 0 rgba(0, 0, 0, 0.45) ;
}

