.global-nav-list a + .extend-global-nav {
  position: absolute;
  border: 1px solid white;
  visibility: hidden;
  box-sizing: border-box;
  -webkit-box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.7) ;
