  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.7) ;
  opacity: 0;
  filter: alpha(opacity=0);
  -webkit-transition: 0.5s;
  -moz-transition: 0.5s;
  -ms-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
  z-index: 200;
}

.global-nav-list a + .extend-global-nav:before {
  position: absolute;
  top: -14px;
  left: 20%;
  margin-left: -13px;
  width: 0;
  height: 0;
  border: 7px solid transparent;
  border-bottom-color: white;
  content: "";
}

.global-nav-list a + .extend-global-nav:after {
