//------------------------------------------------------------
// footer
//------------------------------------------------------------

.footer-area {
	border-top: 1px solid colors(col-blue);
	@include break-point(mobile) {
		width: $pcContentsWidth;
	};
	&-inner {
		width: $pcContentsWidth;
		margin: 0 auto;
		padding: 10px 0;
		@include fz(14);
		@include break-point(tblandscape) {
			display: block;
			width: $tbContentsWidth;
			@include padding(null 10px null 10px);
			box-sizing: border-box;
		};
		@include break-point(mobile) {
			width: $pcContentsWidth;
			@include padding(null 0 null 0);
		};
	}
}

.footer-site-tree {
	&-block {
		display: table;
		width: 100%;
		margin: 0 0 20px;
		table-layout: fixed;
	}
	&-box {
		@include table-cell;
		padding-right: 10px;
		vertical-align: top;
		&:last-child {
			padding-right: 0;
		}
	}
	&-list {
		margin-bottom: 5px;
		&:last-child {
			margin-bottom: 0;
		}
		&-body.maker-list {
			display: table;
			width: 100%;
			.maker-list-group {
				display: table-cell;
				width: 25%;
				padding-right: 10px;
				vertical-align: top;
				&:last-child {
					padding-right: 0;
				}
			}
		}
	}
}

.copyright {
	&-block {
		display: table;
		width: 100%;
		@include fz(10);
	}
	&-box {
		display: table-cell;
		vertical-align: top;
	}
}

.privacy-security-box {
	display: table-cell;
	text-align: right;
	vertical-align: top;
}