//------------------------------------------------------------
// breadcrumbs
//------------------------------------------------------------

.breadcrumbs {
	&-area {
		width: $pcContentsWidth;
		margin: 10px auto;
		@include fz(0);
		line-height: 1;
		@include break-point(tblandscape) {
			width: $tbContentsWidth;
			@include padding(null 10px null 10px);
			box-sizing: border-box;
		}
	}
	&-list {
		position: relative;
		@include inline-block;
		margin-right: 10px;
		padding-right: 10px;
		@include fz(12);
		vertical-align: middle;
		&:after {
			@include arw-wedge;
			top: 4px;
			right: 0;
			content: ""
		}
		&:last-child {
			padding-right: 0;
			&:after {
				border: none;
			}
		}
		a {
			vertical-align: top;
		}
	}
}