//------------------------------------------------------------
// header
//------------------------------------------------------------

.header-area {
	padding-top: 10px;
	border-bottom: 3px solid colors(col-blue);
	@include break-point(mobile) {
		width: $pcContentsWidth;
	};
}

.header-area-inner {
	display: table;
	width: $pcContentsWidth;
	margin: 0 auto;
	line-height: 1;
	text-align: left;
	@include break-point(tblandscape) {
		width: $tbContentsWidth;
		@include padding(null 10px null 10px);
		box-sizing: border-box;
	};
	@include break-point(mobile) {
		width: $pcContentsWidth;
		@include padding(null 0 null 0);
	};
}

//=========================
// ロゴ
//=========================

.logo-block {
	@include table-cell;
	padding-bottom: 12px;
	vertical-align: bottom;
	& img {
		width: 173px;
	}
	.logo-sub-txt {
		@include inline-block;
		margin-left: 10px;
		color: colors(col-blue);
		@include fz(24);
		font-weight: 700;
	}
}

.description-box {
	margin-bottom: 10px;
	@include fz(12);
	font-weight: 400;
}

//=========================
// グローバルナビなど
//=========================
// グローバルナビや補足ナビは_nav.scssに記載

.record-badge {
	&-box {
		@include inline-block;
		float: right;
	}
	&-ttl {
		text-align: center;
	}
	&-body {
		@include padding(2px null 2px null);
		@include fz(10);
	}
}