//------------------------------------------------------------
// 通常車Step2（基本的に事故車・商用車と共用。事故車用、商用車用は下部
//------------------------------------------------------------

//=========================
// 入力画面
//=========================

// 基本的に既存のStep2を踏襲してます（css以外にhtmlやjsも）

// 大枠
//------------------------------------------------------------

#steps {
	position: relative;
}

.step {
	width: $pcContentsWidth;
	margin: 0 auto;
	background-color: colors(col-lightyellow);
	@include box-shadow(0px,0px,3px,0px,rgba(colors(col-black),0.3),inset);
	text-align: left;
	@include break-point(tblandscape) {
		width: calc(100% - 20px);
		@include margin(null 10px null 10px);
	}
}

.stepCol {
	@include padding(20px 20px 30px 20px);
	@include fz(12);
	text-align: left;
}

// フォーム内
//------------------------------------------------------------

.assessment-form {
	&-ttl {
		position: relative;
		top: -54px;
	}
	&-step {
		display: table;
		position: relative;
		width: 100%;
		@include margin(null null 10px null);
		background-color: colors(col-xlightgray);
		text-align: center;
		&-list {
			@include table-cell;
			position: relative;
			width: 33%;
			height: 30px;
			@include fz(20);
			font-weight: 700;
			letter-spacing: 2rem;
			line-height: 1;
			text-indent: 2rem;
			vertical-align: middle;
			&:before {
				position: absolute;
				top: 0;
				right: -30px;
				@include arrow(right, 30, colors(col-xlightgray));
				@include box-shadow(-3px,0,0,0,colors(col-xlightgray));
				z-index: 3;
				content: "";
			}
			&:after {
				position: absolute;
				top: 0;
				right: -33px;
				@include arrow(right, 30, colors(col-white));
				@include box-shadow(-3px,0,0,0,colors(col-white));
				z-index: 2;
				content: "";
			}
			&.active {
				background-color: colors(col-blue);
				color: colors(col-white);
				&:before {
					@include arrow(right, 30, colors(col-blue));
					@include box-shadow(-3px,0,0,0,colors(col-blue));
				}
			}
			&:last-child {
				width: auto;
				&:before, &:after {
					border: none;
					@include box-shadow(0,0,0,0,rgba(colors(col-black),0));
				}
			}
		}
	}
}

.input-form-area {
	display: table;
	position: relative;
	width: 100%;
	@include margin(null null 10px null);
	table-layout: fixed;
	input[readonly] {
		padding: 3px;
		height: 26px;
		border: 1px solid colors(col-lightgray);
		background-color: colors(disable);
	}
}

.input-car-block {
	@include table-cell;
	width: 50%;
	border-right: 10px solid transparent;
}

.input-personal-block {
	@include table-cell;
	width: 50%;
	border-left: 10px solid transparent;
}

.ttl-column {
	@include margin(null null 10px null);
	@include padding(null null 2px null);
	border-bottom: 2px solid colors(col-blue);
	color: colors(col-blue);
	@include fz(18);
	font-weight: 700;
}

.table-assess {
	width: 100%;
	@include fz(14);
	border-collapse: collapse;
	@include break-point(tbportrait) {
		display: block;
	}
	tbody {
		@include break-point(tbportrait) {
			display: block;
		}
	}
	tr {
		@include break-point(tbportrait) {
			display: block;
			margin-bottom: 10px;
		}
		.support-only & {
			border-bottom: 1px dotted colors(col-lightgray);
			&:last-child {
				border-bottom: none;
			}
		}
	}
	td, th {
		padding: 8px 4px;
		vertical-align: top;
		@include break-point(tbportrait) {
			display: block;
		}
		.support-only & {
			padding: 12px 8px;
		}
	}
	th {
		width: 94px;
		text-align: right;
		font-weight: 700;
		font-feature-settings : "palt";
		@include break-point(tbportrait) {
			width: 100%;
			padding: 0 4px;
			text-align: left;
		}
		.input-personal-block & {
			width: 106px;
		}
		.support-only & {
			width: 180px;
		}
	}
	td {
		@include break-point(tbportrait) {
			padding: 0 4px;
		}
		img {
			vertical-align: middle;
		}
	}
}

.formparts {
	&-selectcars {
		width: 300px;
		@include break-point(tblandscape) {
			width: 80%;
		}
	}
	&-input {
		&-xshort {
			width: 40px;
		}
		&-short {
			width: 74px;
		}
		&-middle01 {
			width: 200px;
			@include break-point(tblandscape) {
				width: 80%;
			}
		}
		&-middle02 {
			width: 260px;
			@include break-point(tblandscape) {
				width: 80%;
			}
		}
		&-long {
			width: 300px;
			@include break-point(tblandscape) {
				width: 80%;
			}
		}
	}
	&-readonly {
		width: 300px;
		@include break-point(tblandscape) {
			width: 80%;
		}
	}
	&-textarea {
		width: 100%;
		height: 150px;
	}
}

.company {
	&-area {
		@include break-point(tblandscape) {
			@include flex-block;
			-webkit-flex-wrap: wrap;
			flex-wrap: wrap;
			-webkit-justify-content: space-between;
			justify-content: space-between;
			@include fz(0);
		};
		img {
			border: 1px solid colors(link);
			vertical-align: top;
		}
	}
	&-list {
		@include inline-block;
		width: 215px;
		@include margin(5px 15px 5px 15px);
		vertical-align: top;
		@include break-point(tblandscape) {
			width: 25%;
			@include margin(5px 0 5px 0);
			@include fz(14);
		};
		@include break-point(tbportrait) {
			width: 50%;
		};
		&:first-child, &:nth-child(5) {
			margin-left: 0;
		}
		&:nth-child(4n) {
			margin-right: 0;
		}
		label {
			@include inline-block;
			@include padding(null null null 20px);
			vertical-align: top;
		}
		img {
			width: 88px;
			height: 31px;
		}
		&-block {
			@include margin(null null 20px null);
			vertical-align: top;
		}
	}
	&-name {
		@include inline-block;
		width: 120px;
		vertical-align: top;;
		@include break-point(tblandscape) {
			width: calc(100% - 110px);
		};
		.confirm  & {
			padding: 0;
		}
	}
}

.confirm-pretext {
	clear: both;
	@include margin(10px null 10px null);
	@include fz(14);
	font-weight: 700;
	text-align: center;
}

.submitbtn-area {
	clear: both;
	text-align: center;
	.btn-cv {
		width: auto;
		@include margin(null 20px null 20px);
		padding: 20px 30px;
		.cv-btn-txt {
			@include margin(null null 0 0);
			@include fz(32);
		}
	}
	.btn-back {
		@include inline-block;
		@include margin(null 20px null 20px);
		color: colors(basetxt);
		@include fz(18);
		font-weight: 700;
		text-decoration: none;
		vertical-align: middle;
	}
}

// 入力サポートふきだし
//------------------------------------------------------------

.input-assistant-box {
	position: relative;
}

.assistant-popup {
	&-box {
		position: absolute;
		display: none;
		bottom: -45px;
		left: 4px;
		z-index: 10;
		width: 146%;
	}
	&-balloon {
		@include inline-block;
		position: relative;
		@include padding(6px 10px 6px 6px);
		border: 4px solid colors(col-orange);
		@include border-radius(2px,2px,2px,2px);
		background: colors(col-gray);
		color: colors(col-white);
		@include fz(16);
		line-height: 22px;
		vertical-align: bottom;
		@include box-shadow(2px,2px,3px,0px,rgba(colors(col-black),0.45));
		&:before {
			position: absolute;
			top: -20px;
			left: 32px;
			border: 6px solid transparent;
			border-bottom: 10px solid colors(col-orange);
			content: "";
		}
	}
	&-icon {
		margin-right: 4px;
		vertical-align: inherit;
	}
}


// ラジオ局選択
//------------------------------------------------------------

.radio-select-list {
	@include inline-block;
	width: 25%;
	margin-bottom: 5px;
	vertical-align: top;
	&-block {
		font-size: 0;
	}
	.label-txt {
		@include fz(14);
		vertical-align: middle;
	}
	select {
		display: none;
		width: 100%;
	}
}

//=========================
// 完了画面
//=========================

// 相場表示
//------------------------------------------------------------

.view-assessment-area {
	@include margin(null null 20px null);
	padding: 10px;
	border: 1px solid colors(disable);
	background-color: colors(col-lightyellow)
}

.user-car-info {
	&-block {
		clear: both;
		@include margin(null null 20px null);
	}
	&-ttl {
		@include fz(16);
		font-weight: 700;
	}
	&-tbl {
		width: 480px;
		border-top: 1px solid colors(col-blue);
		border-left: 1px solid colors(col-blue);
		background-color: colors(col-white);
		@include fz(16);
		@include break-point(tbportrait) {
			width: 100%;
		}
		th, td {
			padding: 3px 6px;
			border-right: 1px solid colors(col-blue);
			border-bottom: 1px solid colors(col-blue);
		}
		th {
			width: 100px;
			background-color: colors(col-blue);
			color: colors(col-white);
		}
		tr:not(:last-child) th {
			border-bottom: 1px solid colors(col-white);
		}
		td {
			background-color: colors(col-white);
		}
	}
}

.user-view-asssessment {
	&-block {
		clear: both;
	}
	&-ttl {
		@include fz(22);
		font-weight: 700;
		text-align: center;
	}
	&-supplytxt {
		@include fz(12);
	}
}

.assessment-price {
	&-box {
		@include margin(null null 10px null);
		padding: 5px;
		border: 1px solid colors(col-blue);
		background: colors(col-white);
		@include fz(22);
		font-weight: 700;
		text-align: center;
	}
	&-txt {
		@include margin(null null 10px null);
		@include fz(22);
		font-weight: 700;
		text-align: center;
		text-shadow: 0 0 3px colors(col-white),0 0 3px colors(col-white),0 0 3px colors(col-white),0 0 3px colors(col-white),0 0 3px colors(col-white),0 0 3px colors(col-white),0 0 3px colors(col-white),0 0 3px colors(col-white),0 0 3px colors(col-white),0 0 3px colors(col-white),0 0 3px colors(col-white),3px 3px 1px rgba(colors(col-black), 0.8);
	}
}

.price-strong {
	color: colors(col-red);
	@include fz(48);
}

// サンクス画面で使われるテキスト
//------------------------------------------------------------

.thanks-fixed-txt {
	@include fz(12);
	&-ttl {
		color: colors(ttl);
		font-weight: 700;
		&:before {
			content: "■";
		}
	}
	&-body {
		@include margin(null null 1em 1em);
	}
}

// サンクスページのPR
//------------------------------------------------------------

.thanks-pr {
	&-area {
		@include margin(null null 20px null);
		padding: 10px 0;
		background-color: colors(col-lightyellow);
		text-align: center;
		line-height: 1.5;
		@include break-point(tbportrait) {
			padding: 10px;
		}
		img {
			width: auto;
			max-width: 100%;
			height: auto;
			vertical-align: bottom;
		}
	}
	&-ttl {
		padding: 0 100px;
		text-align: left;
		@include break-point(tbportrait) {
			padding: 0;
		}
	}
	&-body:not(:last-child) {
		@include margin(null null 20px null);
	}
}

// サイト回遊バナー
//------------------------------------------------------------

.mysite-bnr {
	&-area {
		text-align: left;
		@include break-point(tbportrait) {
			padding: 0 10px;
		}
		figure {
			@include margin(30px null 30px null);
		}
		img {
			width: auto;
			max-width: 100%;
			height: auto;
			vertical-align: bottom;
		}
	}
}

.service-announce-area {
	margin: 30px 0;
	border-top: 1px dotted colors(col-lightgray);
	border-bottom: 1px dotted colors(col-lightgray);
	text-align: left;
	&-inner {
		margin: 30px 0;
	}
	.thanks-fixed-txt {
		@include fz(16);
		&-ttl {
			color: colors(basetxt);
			font-weight: 700;
			&:before {
				content: "■";
			}
		}
	}
}

// 矢印付きブロックリンク
//------------------------------------------------------------

.text-with-arw-list {
	margin-bottom: 10px;
	&:last-child {
		margin-bottom: 0;
	}
	.horizon-link & {
		@include inline-block;
		@include margin(10px 10px 10px null);
		&:last-child {
			margin-right: 0;
		}
	}
	a {
		@extend .ico-arw-wedge;
	}
}

//------------------------------------------------------------
// 事故車Step2
//------------------------------------------------------------

.hidden-input-item {
	display: none;
}

.table-assess {
	th {
		.jiko & {
			width: 130px;
		}
		.jiko .input-personal-block & {
			width: 106px;
		}
	}
}
.formparts {
	&-selectcars {
		.jiko & {
			width: 270px;
			@include break-point(tblandscape) {
				width: 80%;
			}
		}
	}
}

//------------------------------------------------------------
// 商用車Step2
//------------------------------------------------------------

.table-assess {
	th {
		.shouyou & {
			width: 120px;
		}
		.shouyou .input-personal-block & {
			width: 106px;
		}
	}
}
.formparts {
	&-selectcars {
		.shouyou & {
			width: 280px;
			@include break-point(tblandscape) {
				width: 80%;
			}
		}
	}
}

.free-input {
	&-txt-block {
		margin: 10px 0;
		text-align: center;
	}
	&-btn {
		color: colors(link);
		text-decoration: underline;
		cursor: pointer;
	}
}

.free-input-block,
.for-type-truck-input,
.for-type-bus-input,
.for-type-kenki-input,
.for-type-fork-input {
	display: none;
}

//------------------------------------------------------------
// お問い合わせ
//------------------------------------------------------------

.contact-caution-area {
	margin-bottom: 20px;
	padding: 10px;
	background-color: colors(col-lightyellow);
	@include fz(14);
}

.contact-tbl {
	width: 100%;
	margin-bottom: 20px;
	// 基本設定
	tr {
		border-bottom: 1px dotted colors(col-lightgray);
	}
	th, td {
		padding: 15px;
		border-right: 1px dotted colors(col-lightgray);
		vertical-align: top;
		box-sizing: border-box;
		&:not(:first-child) {
			width: 65%;
		}
		&:last-child {
			border-right: none;
		}
	}
	th {
		text-align: center;
		&.required {
			position: relative;
			padding-right: 50px;
			&:after {
				position: absolute;
				top: 20px;
				right: 10px;
				padding: 5px;
				background-color: colors(col-red);
				color: colors(col-white);
				line-height: 1;
				@include fz(12);
				content: "必須";
			}
		}
	}
	tbody {
		tr {
			&:last-child {
				border-bottom: none;
			}
		}
	}
}

//------------------------------------------------------------
// 査定キャンセル
//------------------------------------------------------------

.cancel-step {
	width: $pcContentsWidth;
	margin: 0 auto;
	text-align: left;
	@include break-point(tblandscape) {
		width: calc(100% - 20px);
		@include margin(null 10px null 10px);
	}
}

.cancel-tbl {
	width: 100%;
	margin-bottom: 20px;
	// 基本設定
	tr {
		border-bottom: 1px dotted colors(col-lightgray);
	}
	th, td {
		padding: 10px;
		border-right: 1px dotted colors(col-lightgray);
		vertical-align: top;
		box-sizing: border-box;
		&:not(:first-child) {
			width: 65%;
		}
		&:last-child {
			border-right: none;
		}
	}
	th {
		width: 20%;
		text-align: left;
	}
	tbody {
		tr {
			&:last-child {
				border-bottom: none;
			}
		}
	}
}

.news-cancel-area {
	margin-top: 50px;
	padding-top: 20px;
	border-top: 1px solid colors(col-gray);
}