// ボタン
//
// ページ内に使われているボタンについて。<br>
// ページ見出し、セクション毎の見出し、セクション内のさらに小見出しの3つに大きく分かれる。<br>
// メーカー検索ページではメーカー説明用の見出しを使っている。<br>
// コラムおよび初心者ガイドではアイキャッチ画像用の見出しを使っている。
//
// Markup:
// <p>通常のボタン.btn-blue</p>
// <p><a href="javascript:;" class="btn-blue">ボタン</a></p>
// <p>サブ的意味合いボタン.btn-gray</p>
// <p><a href="javascript:;" class="btn-gray">ボタン</a></p>
// <p>コンバージョンボタン.btn-cv</p>
// <p><a href="javascript:;" class="btn-cv">お申込はこちら</a></p>
// <p>実際は↓こんな風に使います</p>
// <p><a href="javascript:;" class="btn-cv">
//   <span class="ico-free">無料</span>
//   <span class="cv-btn-txt">カンタンWEB申込みスタート</span>
//   <span class="cv-steps-flow-box"><span class="cv-steps-flow-cell">郵便番号入力</span><span class="cv-steps-flow-cell">車種・個人情報入力</span><span class="cv-steps-flow-cell">確認画面</span></span>
//   <span class="ficon-keyboard_arrow_right ico-arrow"></span></a></p>
// <p>ページトップに戻るボタン.btn-pagetop</p>
// <p><a href="javascript:;" class="btn-pagetop" style="position: static;"><span class="ficon-keyboard_arrow_up fsize-48 ico-arrow"></span>PAGETOP</a></p>
//
// Styleguide 3.0.0

//------------------------------------------------------------
// Buttons
//------------------------------------------------------------

//=========================
// ボタン全般
//=========================

.btn {
	// メイン要素ボタン
	//------------------------------------------------------------
	&-blue {
		@include btn;
		color: colors(col-white);
		background: colors(link);
		text-shadow: -1px -1px 0px rgba(colors(col-black), 0.4);
		&:link,&:visited {
			color: colors(col-white);
		}
	}
	// サブ要素ボタン
	//------------------------------------------------------------
	&-gray {
		@include btn;
		color: colors(basetxt);
		background: colors(col-xlightgray);
		&:link,&:visited {
			color: colors(basetxt);
		}
	}
	// コンバージョン要素ボタン
	//------------------------------------------------------------
	&-cv {
		@include btn;
		width: 100%;
		padding: 10px 8px;
		color: colors(col-white);
		@include multiple-gradient(rgba(255,127,127,1),rgba(230,23,23,1),rgba(230,23,23,1),rgba(178,0,0,1));
		text-shadow: 1px 1px 1px rgba(0,0,0,0.4);
		&:link,&:visited {
			color: colors(col-white);
		}
		.ico-free {
			position: absolute;
			top: 50%;
			left: 10px;
			width: 70px;
			height: 50px;
			padding-top: 12px;
			background-color: colors(col-white);
			@include border-radius(50%,50%,50%,50%);
			color: colors(col-red);
			@include fz(26);
			font-weight: 700;
			box-sizing: border-box;
			@include box-shadow(1px,1px,1px,0px,rgba(0,0,0,0.4));
			text-shadow: none;
			@include translate(0, -50%);
		}
		.cv-btn-txt {
			display: block;
			clear: both;
			@include margin(null null 10px 40px);
			@include fz(26);
			font-weight: 700;
			pointer-events: none;
			&.assessment-lead {
				margin: 0;
				line-height: 1.5;
			}
		}
		.cv-steps-flow-box {
			clear: both;
			display: block;
			margin-left: 40px;
			@include fz(12);
			pointer-events: none;
			.cv-steps-flow-cell {
				@include inline-block;
				position: relative;
				padding: 2px 2px 2px 14px;
				background: colors(col-gray);
				color: colors(disable);
				text-shadow: none;
				vertical-align: middle;
				box-sizing: border-box;
				&:before {
					position: absolute;
					top: 50%;
					right: -16px;
					margin-top: -8px;
					@include arrow(right, 16 ,colors(col-gray));
					@include box-shadow(-2px,0,0,0,colors(col-gray));
					content: "";
					z-index: 2;
				}
				&:after {
					position: absolute;
					top: 50%;
					right: -18px;
					margin-top: -8px;
					@include arrow(right, 16 ,colors(col-red));
					@include box-shadow(-2px,0,0,0,colors(col-red));
					content: "";
					z-index: 1;
				}
				&:first-child {
					padding-left: 5px;
					background: colors(col-white);
					color: colors(col-gray);
					&:before {
						@include arrow(right, 16 ,colors(col-white));
						@include box-shadow(-2px,0,0,0,colors(col-white));
					}
				}
				&:last-child {
					padding-right: 5px;
					&:before, &:after {
						border: none;
						@include box-shadow(0,0,0,0,rgba(colors(col-black),0));
					}
				}
			}
		}
		.ico-arrow {
			position: absolute;
			top: 50%;
			right: -20px;
			margin-top: -40px;
			color: colors(col-white);
			@include fz(80);
			@include scale(0.5, 1);
			pointer-events: none;
		}
		// widthを自由にする
		&.auto-width {
			width: auto;
		}
		// ステップ数が無い場合
		&.no-step {
			.cv-btn-txt {
				margin: 0 auto;
				@include padding(null 30px null 10px);
			}
			.ico-arrow {
				right: 0;
				margin-top: -20px;
				@include fz(40);
				@include scale(1, 1);
			}
		}
		// サブカラムの場合
		.sub-content & {
			.cv-btn-txt {
				margin: 0;
				@include fz(22);
				@include break-point(tblandscape) {
					@include fz(18);
				};
				@include break-point(tbportrait) {
					@include fz(22);
				};
			}
			.ico-arrow {
				right: -5px;
				margin-top: -15px;
				@include fz(30);
				@include scale(0.5, 1);
			}
		}
	}
	// ページトップボタン
	//------------------------------------------------------------
	&-pagetop {
		@include inline-block;
		position: fixed;
		right: 10px;
		bottom: -100px;
		padding: 15px 10px;
		@include border-radius(3px,3px,3px,3px);
		background-color: colors(col-xlightgray);
		@include fz(12);
		font-weight: 700;
		line-height: 1;
		text-align: center;
		text-decoration: none;
		z-index: 50;
		&:link {
			color: colors(basetxt);
		}
		.ico-arrow {
			display: block;
			clear: both;
			margin: -10px 0;
			color: colors(basetxt);
		}
	}
}